*[data-zoom],
*[data-zoom] * {
  cursor: pointer;
  cursor: zoom-in;
}

.zoom-container {
  cursor: pointer;
  cursor: zoom-out;

  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba($black, 1);
}

.zoom-container::before {
  @include spinner;
}

.zoom-media {
  cursor: pointer;
  cursor: zoom-out;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  width: 100%;;
  max-width: calc(100vw - #{$gutter * 2});
  max-height: calc(100vh - #{$gutter * 2});
}

img.zoom-media {
  width: auto;
}

.zoom-media iframe {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  transform: translate(-50%, -50%);
  max-width: calc(100vw - #{$gutter * 2});
  max-height: calc(100vh - #{$gutter * 2});
}

// NOTE: this pseudo-element allows capturing click event on iframe,
// without interfering with Vimeo's toolbar
.zoom-media::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 80px;
  max-height: calc(100vh - 80px);
}
